import React, { useEffect, useState, useRef } from "react";
import * as d3 from "d3";

export default props => {
  const [dataset, setDataset] = useState(generateDataset());
  const ref = useRef();

  useEffect(() => {
    let svg = d3.select(ref.current);

    var margin = { top: 10, right: 10, bottom: 50, left: 50 };
    var width = +svg.attr("width") - margin.left - margin.right;
    var height = +svg.attr("height") - margin.top - margin.bottom;

    var g = svg
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    g
      .append("text")
      .attr("x", 0)
      .attr("y", 5)
      .attr("text", "Text");

    // Scales
    var x = d3.scaleLinear().range([0, width]);
    var y = d3.scaleLinear().range([height, 0]);

    // Axes
    var xAxisCall = d3.axisBottom(x);
    var xAxis = g
      .append("g")
      .attr("class", "x-axis")
      .attr("transform", "translate(" + 0 + "," + height / 2 + ")");

    var yAxisCall = d3.axisLeft(y);
    var yAxis = g
      .append("g")
      .attr("class", "y-axis")
      .attr("transform", "translate(" + width / 2 + "," + 0 + ")");

    // Labels
    xAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "translate(" + width + ", 0)")
      .attr("y", -6)
      .text("Grade Point Average");
    yAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "rotate(-90)")
      .attr("y", 16)
      .text("Height / Centimeters");

    var flag = true;

    // Run this code every second...
    d3.interval(function() {
      // Flick between our two data arrays
      //let data = flag ? data0 : data1;
      let data = generateDataset();
      // Update our chart with new data
      update(data);

      // Update our flag variable
      flag = !flag;
    }, 3000);

    // Run for the first time
    update(data0);

    function update(data) {
      // Standard transition for our visualization
      var t = d3.transition().duration(750);

      // Update our scales
      x.domain([-5, 5]);
      y.domain([-5, 5]);

      // Update our axes
      xAxis.transition(t).call(xAxisCall);
      yAxis.transition(t).call(yAxisCall);

      // Update text
      // let legend = g.selectAll("text");

      // legend
      //   .exit()
      //   .transition(t)
      //   .attr("fill-opacity", 0.1)
      //   .remove();

      // legend
      //   .transition(t)
      //   .attr("x", 50)
      //   .attr("y", 50);

      // legend
      //   .enter()
      //   .append("text")
      //   .attr("x", 50)
      //   .attr("y", 50)
      //   .attr("text", "Легенда")
      //   .attr("fill", "grey")
      //   .attr("fill-opacity", 0.1)
      //   .transition(t)
      //   .attr("fill-opacity", 1)
      //   .attr("y", 50);

      // Update our circles
      var circles = g.selectAll("circle").data(data);

      circles
        .exit()
        .transition(t)
        .attr("fill-opacity", 0.1)
        .attr("cy", y(0))
        .remove();

      circles
        .transition(t)
        .attr("cx", function(d) {
          return x(d.gpa);
        })
        .attr("cy", function(d) {
          return y(d.height);
        });

      circles
        .enter()
        .append("circle")
        .attr("cx", function(d) {
          return x(d.gpa);
        })
        .attr("cy", y(0))
        .attr("r", 5)
        .attr("fill", "grey")
        .attr("fill-opacity", 0.1)
        .transition(t)
        .attr("fill-opacity", 1)
        .attr("cy", function(d) {
          return y(d.height);
        });
    }
  }, [dataset]);

  return (
    <svg
      width="700"
      height="600"
      //    viewBox="0 0 800 800"
      ref={ref}
    />
  );
};

let data0 = [
  { gpa: 3.42, height: 138 },
  { gpa: 3.54, height: 153 },
  { gpa: 3.14, height: 148 },
  { gpa: 2.76, height: 164 },
  { gpa: 2.95, height: 162 },
  { gpa: 3.36, height: 143 }
];

let data1 = [
  { gpa: 3.15, height: 157 },
  { gpa: 3.12, height: 175 },
  { gpa: 3.67, height: 167 },
  { gpa: 3.85, height: 149 },
  { gpa: 2.32, height: 165 },
  { gpa: 3.01, height: 171 },
  { gpa: 3.54, height: 168 },
  { gpa: 2.89, height: 180 },
  { gpa: 3.75, height: 153 }
];

const generateDataset = () =>
  Array(100)
    .fill(0)
    .map(() => ({
      gpa: Math.random() * 10 - 5,
      height: Math.random() * 10 - 5
    }));
