import React, { useEffect, useState, useRef } from "react";
import * as d3 from "d3";

export default props => {
  const [dataset, setDataset] = useState(generateDataset());
  const ref = useRef();

  useEffect(() => {
    let svg = d3.select(ref.current);

    var margin = { top: 10, right: 10, bottom: 50, left: 50 };
    var width = +svg.attr("width") - margin.left - margin.right;
    var height = +svg.attr("height") - margin.top - margin.bottom;

    var g = svg
      .append("g")
      .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

    let data = [
      { "date": "2012-03-20", "total": 3 },
      { "date": "2012-05-21", "total": 8 },
      { "date": "2012-07-22", "total": 2 },
      { "date": "2012-08-23", "total": 10 },
      { "date": "2012-09-24", "total": 3 },
      { "date": "2012-10-25", "total": 20 },
      { "date": "2012-12-26", "total": 12 }];

    var y = d3.scaleLinear()
      .domain([0, d3.max(data, function (d) { return d.total; })])
      .range([height, 0]);

    var x = d3.scaleTime()
      .domain([new Date(2012, 0, 1), new Date(2013, 0, 1)])
      .rangeRound([0, width]);

    //var x = d3.scaleLinear().range([0, width]);
    // var y = d3.scaleLinear().range([height, 0]);


    // Axes
    var xAxisCall = d3.axisBottom(x);
    var xAxis = g
      .append("g")
      .attr("class", "x-axis")
      .attr("transform", "translate(" + 0 + "," + height + ")");

    var yAxisCall = d3.axisLeft(y);
    var yAxis = g
      .append("g")
      .attr("class", "y-axis")
      .attr("transform", "translate(" + 0 + "," + 0 + ")");

    var t = d3.transition().duration(750);
    xAxis.transition(t).call(xAxisCall);
    yAxis.transition(t).call(yAxisCall);

    // Labels
    xAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "translate(" + 0 + ", 0)")
      .attr("y", -6)
      .text("Date");
    yAxis
      .append("text")
      .attr("class", "axis-title")
      .attr("transform", "rotate(-90)")
      .attr("y", 50)
      .text("Total");

    svg.selectAll('.chart')
      .data(data)
      .enter().append('rect')
      .attr('fill', 'lime')
      .attr('x', function (d) { return x(new Date(d.date)); })
      .attr('y', function (d) { return y(d.total) })
      .attr('width', 10)
      .attr('height', function (d) { return height - y(d.total) });


  }, [dataset]);

  return (
    <svg
      width="700"
      height="600"
      //    viewBox="0 0 800 800"
      ref={ref}
    />
  );
};


