import React, { useEffect, useState, useRef } from "react";
import * as d3 from "d3";

export default (props) => {
    // const [dataset, setDataset] = useState(
    //     generateDataset()
    // )
    const ref = useRef()

    useEffect(() => {
        // let allwidth = 600;
        // let allheight = 500;
        // let margin = 200;

        let data = props.dataset || [{ name: "Apple", value: 100 }, { name: "Google", value: 400 }, { name: "Sber", value: 300 }];

        //const svg = d3.select(ref.current);
        // let width = allwidth - margin;
        // let height = allheight - margin;

        let width = 600,
            barHeight = 20,
            margin = 1;

        var scale = d3.scaleLinear()
            .domain([100, 500])
            .range([650, 0]);

        var svg = d3.select(ref.current)
            .append("svg")
            .attr("width", width)
            .attr("height", barHeight * data.length);

        var g = svg.selectAll("g")
            .data(data)
            .enter()
            .append("g")
            .attr("transform", function (d, i) { return "translate(0," + i * barHeight + ")"; });

        g.append("rect")
            .attr("x", function (d, i) { return width - d.value / 2 }) //scale(d.value)
            .attr("y", 0)
            .attr("width", function (d, i) { return scale(d.value) }) //function (d) { return scale(d.value); }
            .attr("height", barHeight - margin)

        g.append("text")
            .attr("fill", "darkblue")
            .attr("x", 0)
            .attr("y", function (d, i) { return barHeight / 2; })
            .attr("dy", ".35em")
            .text(function (d) { return d.name + " " + d.value; });





    }, [props.dataset])

    return (
        <svg
            viewBox="0 0 600 500"
            ref={ref}
            fill="#bbb"
        />
    )
}

const generateDataset = () => (
    Array(10).fill(0).map((item, index) => ([
        index + 2011,
        Math.random() * 90 + 10,
    ]))
)

